<template>
  <div id="app">
    {{test}}
    <img alt="Vue logo" src="./assets/logo.png">
    <HelloWorld msg="Welcome to Your Vue.js App"/>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld.vue'
import {test} from './src/Api/Repository/App'

export default {
  name: 'App',
  components: {
    HelloWorld
  },
  data () {
    return {
      test: ''
    }
  },
  mounted () {
    this.mTest()
  },
  methods: {
    async mTest () {

      this.test = await test()
    }
  }
}
</script>

