import axios from 'axios'
// import {apiErrorHandler} from '@/Api/ApiErrorHandler'
// import store from '@/store'
// import Cookies from 'js-cookie'

// const apiVer = process.env.API_VER
// const baseUrl = `${process.env.JS_RUNTIME_WEB_API_URL}/api/${apiVer}`
// const debug = process.env.API_CONSOLE_LOG === 'true'
const baseUrl = `https://food-score.tech/api/v1`

const consoleLog = (out) => {
  // if (true) {
    console.log(out)
  // }
}

const client = axios.create({
  baseURL: baseUrl,
  // withCredentials: true,
  timeout: 30000
})
client.interceptors.request.use(
  config => {
    // if (true) {
      console.group(`>>> REQ [${config.method.toUpperCase()}] ${config.url}`)
      console.dir(config)
      console.groupEnd()
    // }
    // const conf = store.getters.headerConfig
    // config.headers['api-token'] = process.env.JS_RUNTIME_API_TOKEN
    // config.headers['access-token'] = conf.headers['access-token']
    // config.headers['client'] = conf.headers['client']
    // config.headers['uid'] = conf.headers['uid']
    // config.headers['maintenance-token'] = Cookies.get('maintenance_token')
    return config
  },
  error => {
    consoleLog(error)
    return Promise.reject(error)
  }
)
// response interceptor
client.interceptors.response.use(
  response => {
    // if (true) {
      console.group(`<<< RES [${response.config.method.toUpperCase()}] ${response.config.url}`)
      console.dir(response)
      console.groupEnd()
    // }
    return response
  },
  // error => {
    // return apiErrorHandler.onError(error)
  // }
)

const ApiService = {
  // get: (url, header) => {
  get: (url) => {
    return client.get(baseUrl + url)
      .then(
        resolve => {
          return resolve
        }
      )
      .catch(
        reject => {
          return reject
        }
      )
  },
  // post: (url, params, header) => {
  post: (url, params) => {
    return client.post(baseUrl + url, params)
      .then(
        resolve => {
          return resolve
        }
      )
      .catch(
        reject => {
          return reject
        }
      )
  },
  /**
   * PUT APIを実行する
   * @param url
   * @param params
   * @param header
   * @return {AxiosPromise<any>}
   */
  // put: (url, params, header) => {
  put: (url, params) => {
    return client.put(baseUrl + url, params)
      .then(
        resolve => {
          return resolve
        }
      )
      .catch(
        reject => {
          return reject
        }
      )
  },
  /**
   * PATCH APIを実行する
   * @param url
   * @param params
   * @return {AxiosPromise<any>}
   */
  patch: (url, params) => {
    return client.patch(baseUrl + url, params)
      .then(
        resolve => {
          return resolve
        }
      )
      .catch(
        reject => {
          return reject
        }
      )
  },
  delete: (url, params = null) => {
    if (!params) {
      return client.delete(baseUrl + url)
        .then(
          resolve => {
            return resolve
          }
        )
        .catch(
          reject => {
            return reject
          }
        )
    }
    return client.delete(baseUrl + url, {data: params})
      .then(
        resolve => {
          return resolve
        }
      )
      .catch(
        reject => {
          return reject
        }
      )
  }
}

export default ApiService
